___CSS_LOADER_EXPORT___.push([module.id, `/* Terminal Theme - Green on Black */
:root {
  --bg-primary: #0a0a0a;
  --bg-secondary: #1a1a1a;
  --bg-tertiary: #2a2a2a;
  --text-primary: #00ff00;
  --text-secondary: #00cc00;
  --text-dim: #008800;
  --border-color: #00ff00;
  --accent: #00ffff;
  --error: #ff0040;
  --warning: #ffff00;
  --success: #00ff00;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Courier New', Courier, monospace;
  background-color: var(--bg-primary);
  color: var(--text-primary);
  overflow: hidden;
  height: 100vh;
}

/* App Container */
.terminal-app {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

/* Header Bar */
.header-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--bg-secondary);
  border-bottom: 2px solid var(--border-color);
  padding: 0.5rem 1rem;
  height: 40px;
}

.header-title {
  font-size: 1.1rem;
  font-weight: bold;
  text-shadow: 0 0 5px var(--text-primary);
}

.header-status {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.status-indicator {
  font-size: 0.8rem;
  animation: pulse 2s infinite;
}

.status-indicator.connected {
  color: var(--success);
}

.status-indicator.connecting {
  color: var(--warning);
}

.status-indicator.disconnected {
  color: var(--error);
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

/* Main Container */
.main-container {
  display: flex;
  flex: 1;
  overflow: hidden;
}

/* Panels */
.panel {
  display: flex;
  flex-direction: column;
  background-color: var(--bg-secondary);
  border: 1px solid var(--border-color);
  margin: 0.5rem;
}

.panel-left {
  width: 300px;
  min-width: 250px;
}

.panel-center {
  flex: 1;
  min-width: 400px;
}

.panel-right {
  width: 250px;
  min-width: 200px;
}

.panel-header {
  background-color: var(--bg-tertiary);
  padding: 0.5rem;
  border-bottom: 1px solid var(--border-color);
  font-weight: bold;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.panel-content {
  flex: 1;
  overflow-y: auto;
  padding: 1rem;
}

/* Sections */
.section {
  margin-bottom: 1.5rem;
}

.section h3 {
  color: var(--accent);
  margin-bottom: 0.5rem;
  text-transform: uppercase;
  font-size: 0.9rem;
}

.info-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
  font-size: 0.9rem;
}

.label {
  color: var(--text-secondary);
}

.value {
  color: var(--text-primary);
  font-family: monospace;
}

/* Terminal Output */
.terminal-output {
  flex: 1;
  overflow-y: auto;
  padding: 1rem;
  font-family: 'Courier New', Courier, monospace;
  line-height: 1.4;
}

.system-message {
  color: var(--accent);
  margin-bottom: 1rem;
  white-space: pre-wrap;
}

.chat-message {
  margin-bottom: 0.5rem;
}

.message-timestamp {
  color: var(--text-dim);
  font-size: 0.8rem;
}

.message-sender {
  color: var(--text-secondary);
  font-weight: bold;
}

.message-content {
  color: var(--text-primary);
}

.error-message {
  color: var(--error);
}

.success-message {
  color: var(--success);
}

/* Terminal Input */
.terminal-input-container {
  display: flex;
  align-items: center;
  padding: 0.5rem 1rem;
  border-top: 1px solid var(--border-color);
  background-color: var(--bg-tertiary);
}

.input-prompt {
  color: var(--text-secondary);
  margin-right: 0.5rem;
  font-weight: bold;
}

.terminal-command-input {
  flex: 1;
  background: transparent;
  border: none;
  color: var(--text-primary);
  font-family: 'Courier New', Courier, monospace;
  font-size: 1rem;
  outline: none;
}

.terminal-command-input::placeholder {
  color: var(--text-dim);
}

/* Inputs and Buttons */
.terminal-input {
  background-color: var(--bg-tertiary);
  border: 1px solid var(--border-color);
  color: var(--text-primary);
  padding: 0.3rem 0.5rem;
  font-family: monospace;
  width: 100%;
  outline: none;
}

.terminal-input:focus {
  border-color: var(--accent);
  box-shadow: 0 0 5px var(--accent);
}

.terminal-button {
  background-color: var(--bg-tertiary);
  border: 1px solid var(--border-color);
  color: var(--text-primary);
  padding: 0.3rem 1rem;
  font-family: monospace;
  cursor: pointer;
  transition: all 0.2s;
  text-transform: uppercase;
}

.terminal-button:hover {
  background-color: var(--border-color);
  color: var(--bg-primary);
  box-shadow: 0 0 10px var(--border-color);
}

/* Tabs */
.tabs {
  display: flex;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.tab-button {
  background-color: var(--bg-tertiary);
  border: 1px solid var(--border-color);
  color: var(--text-dim);
  padding: 0.3rem 0.8rem;
  cursor: pointer;
  font-family: monospace;
  font-size: 0.9rem;
  transition: all 0.2s;
}

.tab-button.active {
  color: var(--text-primary);
  border-bottom: 2px solid var(--accent);
}

.tab-button:hover {
  color: var(--text-primary);
}

/* User List */
.user-list {
  margin-bottom: 1rem;
}

.user-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  margin-bottom: 0.3rem;
  background-color: var(--bg-tertiary);
  border: 1px solid transparent;
  cursor: pointer;
  transition: all 0.2s;
}

.user-item:hover {
  border-color: var(--border-color);
  box-shadow: 0 0 5px var(--border-color);
}

.user-name {
  color: var(--text-primary);
  font-weight: bold;
}

.user-status {
  font-size: 0.7rem;
  color: var(--success);
}

/* Add Friend Container */
.add-friend-container {
  display: flex;
  gap: 0.5rem;
  margin-top: 1rem;
}

/* Debug Panel */
.debug-panel {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 300px;
  background-color: var(--bg-secondary);
  border-top: 2px solid var(--border-color);
  z-index: 1000;
  transition: transform 0.3s;
}

.debug-panel.hidden {
  transform: translateY(100%);
}

.debug-info {
  font-size: 0.8rem;
  color: var(--text-dim);
  margin-bottom: 1rem;
  max-height: 150px;
  overflow-y: auto;
}

.debug-controls {
  display: flex;
  gap: 0.5rem;
}

.debug-controls textarea {
  flex: 1;
  min-height: 60px;
  resize: vertical;
}

/* Modal */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
}

.modal.hidden {
  display: none;
}

.modal-content {
  background-color: var(--bg-secondary);
  border: 2px solid var(--border-color);
  width: 90%;
  max-width: 600px;
  max-height: 80vh;
  display: flex;
  flex-direction: column;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  border-bottom: 1px solid var(--border-color);
}

.modal-body {
  padding: 1rem;
  overflow-y: auto;
}

.modal-buttons {
  display: flex;
  gap: 1rem;
  margin-top: 1rem;
}

.close-button {
  background: none;
  border: none;
  color: var(--text-primary);
  font-size: 1.5rem;
  cursor: pointer;
  padding: 0;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.close-button:hover {
  color: var(--error);
}

/* Scrollbar Styling */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: var(--bg-tertiary);
}

::-webkit-scrollbar-thumb {
  background: var(--border-color);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--accent);
}

/* Responsive Design */
@media (max-width: 1024px) {
  .panel-left {
    width: 200px;
    min-width: 150px;
  }
  
  .panel-right {
    width: 180px;
    min-width: 150px;
  }
}

@media (max-width: 768px) {
  .main-container {
    flex-direction: column;
  }
  
  .panel {
    width: 100% !important;
    min-width: unset !important;
    margin: 0.2rem;
  }
  
  .panel-left,
  .panel-right {
    max-height: 200px;
  }
}

/* Utility Classes */
.hidden {
  display: none !important;
}

.fade-in {
  animation: fadeIn 0.3s ease-in;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA,oCAAoC;AACpC;EACE,qBAAqB;EACrB,uBAAuB;EACvB,sBAAsB;EACtB,uBAAuB;EACvB,yBAAyB;EACzB,mBAAmB;EACnB,uBAAuB;EACvB,iBAAiB;EACjB,gBAAgB;EAChB,kBAAkB;EAClB,kBAAkB;AACpB;;AAEA;EACE,sBAAsB;EACtB,SAAS;EACT,UAAU;AACZ;;AAEA;EACE,8CAA8C;EAC9C,mCAAmC;EACnC,0BAA0B;EAC1B,gBAAgB;EAChB,aAAa;AACf;;AAEA,kBAAkB;AAClB;EACE,aAAa;EACb,sBAAsB;EACtB,aAAa;AACf;;AAEA,eAAe;AACf;EACE,aAAa;EACb,8BAA8B;EAC9B,mBAAmB;EACnB,qCAAqC;EACrC,4CAA4C;EAC5C,oBAAoB;EACpB,YAAY;AACd;;AAEA;EACE,iBAAiB;EACjB,iBAAiB;EACjB,wCAAwC;AAC1C;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,WAAW;AACb;;AAEA;EACE,iBAAiB;EACjB,4BAA4B;AAC9B;;AAEA;EACE,qBAAqB;AACvB;;AAEA;EACE,qBAAqB;AACvB;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,WAAW,UAAU,EAAE;EACvB,MAAM,YAAY,EAAE;AACtB;;AAEA,mBAAmB;AACnB;EACE,aAAa;EACb,OAAO;EACP,gBAAgB;AAClB;;AAEA,WAAW;AACX;EACE,aAAa;EACb,sBAAsB;EACtB,qCAAqC;EACrC,qCAAqC;EACrC,cAAc;AAChB;;AAEA;EACE,YAAY;EACZ,gBAAgB;AAClB;;AAEA;EACE,OAAO;EACP,gBAAgB;AAClB;;AAEA;EACE,YAAY;EACZ,gBAAgB;AAClB;;AAEA;EACE,oCAAoC;EACpC,eAAe;EACf,4CAA4C;EAC5C,iBAAiB;EACjB,aAAa;EACb,8BAA8B;EAC9B,mBAAmB;AACrB;;AAEA;EACE,OAAO;EACP,gBAAgB;EAChB,aAAa;AACf;;AAEA,aAAa;AACb;EACE,qBAAqB;AACvB;;AAEA;EACE,oBAAoB;EACpB,qBAAqB;EACrB,yBAAyB;EACzB,iBAAiB;AACnB;;AAEA;EACE,aAAa;EACb,8BAA8B;EAC9B,qBAAqB;EACrB,iBAAiB;AACnB;;AAEA;EACE,4BAA4B;AAC9B;;AAEA;EACE,0BAA0B;EAC1B,sBAAsB;AACxB;;AAEA,oBAAoB;AACpB;EACE,OAAO;EACP,gBAAgB;EAChB,aAAa;EACb,8CAA8C;EAC9C,gBAAgB;AAClB;;AAEA;EACE,oBAAoB;EACpB,mBAAmB;EACnB,qBAAqB;AACvB;;AAEA;EACE,qBAAqB;AACvB;;AAEA;EACE,sBAAsB;EACtB,iBAAiB;AACnB;;AAEA;EACE,4BAA4B;EAC5B,iBAAiB;AACnB;;AAEA;EACE,0BAA0B;AAC5B;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,qBAAqB;AACvB;;AAEA,mBAAmB;AACnB;EACE,aAAa;EACb,mBAAmB;EACnB,oBAAoB;EACpB,yCAAyC;EACzC,oCAAoC;AACtC;;AAEA;EACE,4BAA4B;EAC5B,oBAAoB;EACpB,iBAAiB;AACnB;;AAEA;EACE,OAAO;EACP,uBAAuB;EACvB,YAAY;EACZ,0BAA0B;EAC1B,8CAA8C;EAC9C,eAAe;EACf,aAAa;AACf;;AAEA;EACE,sBAAsB;AACxB;;AAEA,uBAAuB;AACvB;EACE,oCAAoC;EACpC,qCAAqC;EACrC,0BAA0B;EAC1B,